import React from 'react';
import Reg from './components/reg/reg.js'
import Friends from './components/friends/friends.js'
import ErrorMsg from './components/error/error.js'


function App(props) {

	if (props.userData){
		if(props.userData.friends){
			return <Friends userData={props.userData}/>
		}else if (props.userData.error){
			return <ErrorMsg msg={props.userData.error}/>
		}
	}else {
		return <Reg/>
	}

}

export default App;


