import React from 'react';


class ErrorMsg extends React.Component{
	constructor(props){
		super(props);
		this.state = {timer: 5};
		this.clock = this.clock.bind(this);
	}

	clock(){
		if(this.state.timer>0){
			this.setState({
				timer: this.state.timer-1,
			});
		}
	}


	componentDidMount() {
		this.timerID = setInterval(this.clock, 1000)
	}

	componentWillUnmount() {
		clearInterval(this.timerID);
	}

	render(){

	let redirect = 'https://altufjevfrol.github.io/HelloVK';
	let uri =`https://oauth.vk.com/authorize?client_id=7222681&display=page&redirect_uri=${redirect}&scope=friends&response_type=token&v=5.52&revoke=1&`
		return (
			<div className="error_msg">
				<div>Сервер VK ответил с ошибкой!</div>
				<div>
					<code> error code: {this.props.msg.error_code}</code><br></br>
					<code>error msg: {this.props.msg.error_msg}</code></div>
				<div>
					Страница будет перезагружена через <b>{this.state.timer}</b> <br></br>
					Или попробуйте <a href={uri}>авторизоваться</a> еще раз.
				</div>
			</div>
			);
	}
}

export default ErrorMsg;